import * as React from 'react'
import Flicking from '@egjs/react-flicking'
import styles from './List.module.scss'
import img1 from '../../img/list1.jpeg'
import img2 from '../../img/list2.png'
import img3 from '../../img/list3.jpeg'

const List: React.FC = () => {
  const list = [
    {
      id: 1,
      title: '여행가기',
      src: img1,
    },
    {
      id: 2,
      title: '여행가기',
      src: img2,
    },
    {
      id: 3,
      title: '여행가기',
      src: img3,
    },
  ]
  return (
    <Flicking bound gap={10} bounce={0} zIndex={0} className={styles.flicking}>
      {list.map((item) => (
        <div key={`list-${item.id}`} className={styles.item}>
          <button type='button' className={styles.button}>
            <img src={item.src} alt="" className={styles.image} />
            <strong className={styles.title}>{item.title}</strong>
          </button>
        </div>
      ))}
    </Flicking>
  )
}

export default List
