import * as React from 'react'
import { useEffect, useState } from 'react'
import classnames from 'classnames'
import styles from './Header.module.scss'
import logo from '../../img/logo.png'
import thumbnail from '../../img/thumbnail.png'
import date from '../../img/date.png'

const Header: React.FC = () => {
  const [showAni, setShowAni] = useState(false)

  useEffect(() => {
    setShowAni(true)
  }, [])

  return (
    <header className={classnames(styles.header, showAni && styles.animation)}>
      <img src={logo} alt="설♥︎규 #커플 버킷리스트" className={styles.logo} width='300' height='40' />
      <div className={styles.thumbnail}>
        <img src={thumbnail} alt="" className={styles.image} width='100' height='100' />
      </div>
      <img src={date} alt="2021.04.02 ~" className={styles.date} width='150' height='20' />
      <em className={styles.dday}>{}</em>
    </header>
  )
}

export default Header
